.label {
  display: flex;
  flex-direction: column;
  gap: 2px;
  width: 100%;
}

.labelText {
  font-size: 0.8em;
  font-weight: bold;
  margin: 0;
}

.labelError {
  color: red;
}

.errorLabel {
  font-size: 0.4em;
  margin: 0;
  color: red;
}

.input {
  padding: 12px;
  border: 1px solid #ccc;
  width: 100%;
}

.inputError {
  border-color: red;
  color: red;
}
